import React, { useState } from "react";
import {
  useGetCommentsQuery,
  usePostCommentMutation,
  useUpdateCommentMutation,
  useDeleteCommentMutation,
} from "../services/commentsApi";
import styles from "./Comments.module.css";

const Comments = ({ newsId }) => {
  const { data: comments = [], error, isLoading } = useGetCommentsQuery();
  const [postComment] = usePostCommentMutation();
  const [updateComment] = useUpdateCommentMutation();
  const [deleteComment] = useDeleteCommentMutation();

  const [commentText, setCommentText] = useState("");
  const [editCommentId, setEditCommentId] = useState(null);
  const [updatedText, setUpdatedText] = useState("");

  const handleCommentSubmit = async () => {
    try {
      await postComment({ text: commentText, news: newsId }).unwrap();
      setCommentText("");
    } catch (error) {
      console.error("Ошибка при создании комментария:", error);
    }
  };

  const handleUpdateComment = async (id) => {
    try {
      await updateComment({
        id,
        updatedComment: { text: updatedText, news: newsId },
      }).unwrap();
      setEditCommentId(null);
    } catch (error) {
      console.error("Ошибка при обновлении комментария:", error);
    }
  };

  const handleDeleteComment = async (id) => {
    try {
      await deleteComment(id).unwrap();
    } catch (error) {
      console.error("Ошибка при удалении комментария:", error);
    }
  };

  if (isLoading) return <p>Загрузка комментариев...</p>;
  if (error) return <p>Ошибка при загрузке комментариев</p>;

  return (
    <div className={styles.commentsContainer}>
      <h2>Комментарии</h2>
      <ul className={styles.commentsList}>
        {comments.map((comment) => (
          <li key={comment.id} className={styles.commentItem}>
            {editCommentId === comment.id ? (
              <div>
                <textarea
                  className={styles.commentTextarea}
                  value={updatedText}
                  onChange={(e) => setUpdatedText(e.target.value)}
                />
                <button
                  className={`${styles.button} ${styles.submitButton}`}
                  onClick={() => handleUpdateComment(comment.id)}
                >
                  Сохранить
                </button>
              </div>
            ) : (
              <div>
                <div className={styles.commentHeader}>
                  <span className={styles.commentAuthor}>{comment.author}</span>
                  <span className={styles.commentDate}>
                    {new Date(comment.created_at).toLocaleString()}
                  </span>
                </div>
                <p className={styles.commentText}>{comment.text}</p>
                <div className={styles.commentActions}>
                  <button
                    className={`${styles.button} ${styles.editButton}`}
                    onClick={() => setEditCommentId(comment.id)}
                  >
                    Редактировать
                  </button>
                  <button
                    className={`${styles.button} ${styles.deleteButton}`}
                    onClick={() => handleDeleteComment(comment.id)}
                  >
                    Удалить
                  </button>
                </div>
              </div>
            )}
          </li>
        ))}
      </ul>
      <div className={styles.commentForm}>
        <textarea
          className={styles.commentTextarea}
          value={commentText}
          onChange={(e) => setCommentText(e.target.value)}
          placeholder="Оставьте комментарий"
        />
        <button
          className={`${styles.button} ${styles.submitButton}`}
          onClick={handleCommentSubmit}
        >
          Отправить
        </button>
      </div>
    </div>
  );
};

export default Comments;
